// Dependencies
import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import { Transition, TransitionGroup } from 'react-transition-group';
import anime from 'animejs';

// Components
import HomePage      from './pages/Home';
import AboutPage     from './pages/About';
import SkillsPage    from './pages/Skills';
import CodePage      from './pages/Code';
import ContactPage   from './pages/Contact';
import NotFoundPage  from './pages/NotFound';
import MenuButton    from './MenuButton';
import Nav from './Nav';

// Style
import 'flexboxgrid/css/flexboxgrid.min.css';


const duration = 1200;

const animatePageIn = (node, isAppearing) => {
  anime.timeline()
  .add({
    targets: node,
    duration: duration,
    translateX: ['-100%', 0],
    easing: 'easeInOutQuint',
  });
}

const animatePageOut = (node, isAppearing) => {
  anime.timeline()
  .add({
    targets: node,
    duration: duration/2,
    opacity: [1, 0],
    easing: 'easeInOutQuad',
  });
}

export default class App extends Component {
  constructor() {
    super();
    this.state = {
      isMenuOpen: false,
      in: true,
    };
  }

  componentWillMount() {
    window.onresize = this.closeMenu;
  }

  toggleMenu = () => {
    let bodyClassList = document.body.classList;
    
    if (bodyClassList.contains('no-scroll')) {
      document.body.classList.remove('no-scroll');
    } else {
      document.body.classList.add('no-scroll');
    }

    this.setState({isMenuOpen: !this.state.isMenuOpen});
  }

  closeMenu = () => {
    this.setState({isMenuOpen: false});
  }

  render() {
    return (
      <Route render={({ location }) => (
        <div className="App">
          <TransitionGroup>
            <Transition
              appear
              in={ true }
              timeout={ duration }
              onEntering={ (node, isAppearing) => animatePageIn(node, isAppearing) }
              onExiting={ (node, isAppearing) => animatePageOut(node, isAppearing) }
              key={ location.key } >
              <Switch location={ location } >
                <Route exact path='/' component={ HomePage } />
                <Route path='/about' component={ AboutPage } />
                <Route path='/skills' component={ SkillsPage } />
                <Route path='/code' component={ CodePage } />
                <Route path='/contact' component={ ContactPage } />
                <Route component={ NotFoundPage } />
              </Switch>
            </Transition>
          </TransitionGroup>
          <MenuButton onClick={ this.toggleMenu } isMenuOpen={ this.state.isMenuOpen } />
          <Nav isOpen={ this.state.isMenuOpen } onClickMenuItem={ this.closeMenu } />
        </div>
      )} />
    );
  }
};



