import React from 'react';
import { Route } from 'react-router-dom';

// Components
import ContactForm from './ContactForm';
import ThankYou from './ThankYou';

const ContactSection = (props) => {
  return (
    <main className="main-content">
      <section id="contact" className="section contact">
        <div className="wrapper wrapper--center">
          <h2>
            Contact
          </h2>
          <hr />
          <p>
            Une question&nbsp;? Envie d'en savoir plus ou simplement de me laisser un mot au passage&nbsp;?
          </p>
          <p>
            Vous pouvez m'envoyer un email à l'adresse <a href="mailto: thomas@vandenhede.me">thomas@vandenhede.me</a> ou bien remplir le formulaire de contact.
          </p>
          <hr />

          <Route exact path="/contact" component={ ContactForm } />
          <Route exact path="/contact/thank-you" component={ ThankYou } />

        </div>
      </section>
    </main>
  );
};

export default ContactSection;


